import axios from 'axios'
import routes from '@/routes'
import store from '@/vuex/store'
import { Message } from 'element-ui'
import { showFullScreenLoading, tryHideFullScreenLoading } from '@/utils/auth'
import { URL } from '../api/apiSetting'
export const http = axios.create({
  baseURL:URL.base,
  timeout: 1000 * 10,
  headers: {
    'Content-Type': 'application/json',
    'Access-Control-Allow-Origin':'*'
  },
  showLoading:true,
  delayLoading:true
});

// 
http.interceptors.request.use(function (config) {
  config.headers["gTicket"] = store.state.gTicket;
  if(config.showLoading) {
    if(config.delayLoading){
      showFullScreenLoading();
    }else {
      showFullScreenLoading(0);
    }
  }
  if(config.headers['gTicket'] == null ){
      routes.replace({
        path: '/login' // token
      })
  }
  return config
}, function (error) {
  Message.error(',');
  tryHideFullScreenLoading();
  return Promise.reject(error)
});

//
http.interceptors.response.use(response => {
  let code = response.data.code
  //
  if(response && response.headers['content-type']==='application/x-msdownload'){
    return response;
  }
  if(response && response.headers['content-type']==='application/octet-stream'){
    return response;
  }
  tryHideFullScreenLoading()
  if (response && response.data.code !== 0) {
    if(routes.history.current.name !== 'login'){
      Message.error(`${response.data.msg}`)
    }
    if (Object.is(code, -2) || Object.is(code, -11)) {
      store.dispatch('setgTicket', null)
      routes.push('/login')
    } else if (!Object.is(routes.history.current.path, '/login')) {
      return false
    }
  } else if (!response) {
    Message.error(``)
  }
  return response.data;
}, err => {
  tryHideFullScreenLoading();
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        Message.error('');
        break;
      case 404:
        Message.error('');
        break;
      case 500:
        Message.error('');
        break;
      case 401:
        Message.error('');
        break;
      case 403:
        Message.error('');
        break;
      case 408:
        Message.error('');
        break;
      case 502:
        Message.error('');
        break;
      case 503:
        Message.error('');
        break;
      default:
        Message.error(``);
    }
  } else {
    Message.error(` `)
  }
  return false
});

http.all = axios.all;

// Vue 
const httpPlugin = {};
httpPlugin.install = function (Vue, options) {
  Vue.prototype.$http = http
};

export default httpPlugin



