<template>
    <div>
        <el-col :span="24" class="header">
            <el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
                <img src="../../assets/images/logo.png" alt="" @click="toindex">
            </el-col>
            <el-col :span="10">
                <div class="tools item_title">
                    
                </div>
            </el-col>
            <el-col :span="4" class="userinfo" >
                <img src="../../assets/images/userimg.png" />
                <el-dropdown @command="handleCommand"  trigger="click">
                  <div @click="getUserInfo">
                    <span class="el-dropdown-link">
                        {{employeeName}}
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    </div>
                    <el-dropdown-menu slot="dropdown" >
                        <el-dropdown-item command="a"></el-dropdown-item>
                        <el-dropdown-item command="b"></el-dropdown-item>
                        <el-dropdown-item command="c"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-col>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible" class="s-dialog userInfo_dialog" :modal="false">
            <el-row :gutter="32">
                <el-col :span="8">
                    <label></label>
                </el-col>
                <el-col :span="12">
                    <span>{{userInfo.username}}</span>
                </el-col>
            </el-row>
            <el-row :gutter="32">
                <el-col :span="8">
                    <label></label>
                </el-col>
                <el-col :span="12">
                    <span>{{userInfo.employeeName}}</span>
                </el-col>
            </el-row>
            <el-row :gutter="32">
                <el-col :span="8">
                    <label></label>
                </el-col>
                <el-col :span="12">
                    <span>{{userInfo.position}}</span>
                </el-col>
            </el-row>
            <el-row :gutter="32">
                <el-col :span="8">
                    <label></label>
                </el-col>
                <el-col :span="12">
                    <span>{{userInfo.marketName}}</span>
                </el-col>
            </el-row>
            <el-row :gutter="32">
                <el-col :span="8">
                    <label></label>
                </el-col>
                <el-col :span="12">
                    <span>{{userInfo.department}}</span>
                </el-col>
            </el-row>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false" class="btn-reset" type="primary"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="changePwd_dialog" class="s-dialog" :modal="false">
            <el-form :model="pwd_data" :rules="change_rules" ref="change_form">
                <el-form-item label="" :label-width="formLabelWidth" prop="password">
                    <el-input v-model="pwd_data.password" auto-complete="off" type="password"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="newPwd">
                    <el-input v-model="pwd_data.newPwd" auto-complete="off" type="password"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="verifyPwd">
                    <el-input v-model="pwd_data.verifyPwd" auto-complete="off" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button class="btn-save" type="primary" @click="save"></el-button>
                <el-button @click="changePwd_dialog = false" class="btn-reset"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import ApiSetting from "../../api/access-manage";
export default {
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.pwd_data.newPwd) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      collapsed: false,
      dialogFormVisible: false,
      changePwd_dialog: false,
      pwd_data: {
        password: "",
        newPwd: "",
        verifyPwd: ""
      },
      employeeName:"",
      formLabelWidth: "80px",
      change_rules: {
        password: [
          { required: true, message: "", trigger: "blur" }
        ],
        newPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (value) {
                if (
                  /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,18}$/.test(value) ==
                  false
                ) {
                  callback(new Error("6-18"));
                } else {
                  callback();
                }
              }
            },
            trigger: "blur"
          }
        ],
        verifyPwd: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass2, trigger: "blur" }
        ]
      },
      userInfo: {}
    };
  },
  methods: {
    toindex() {
      this.$router.push({ path: "/" });
    },

    // 
    getUserInfo() {
      this.$http({
        method: "get",
        url: ApiSetting.userInfo,
        showLoading: false
      }).then(res => {
        if (res) {
          if(res.data){
            this.userInfo = res.data;
          }
        }
      });
    },
    //
    getUserInfo1() {
      this.$http({
        method: "get",
        url: ApiSetting.userInfo,
        showLoading: false
      }).then(res => {
        if (res) {
          if(res.data){

          }else{
            if(this.timerId) clearInterval(this.timerId);
            this.$message.error('');
            this.$router.push({name:'login'})
          }
        }
      });
    },
    // 
    handleCommand(command) {
      switch (command) {
        case "a":
          this.dialogFormVisible = true;
          break;
        case "b":
          this.changePwd_dialog = true;
          this.$refs["change_form"].resetFields();
          break;
        case "c":
          this.login_out();
          break;
        default:
          break;
      }
    },
    // 
    save() {
      this.$refs["change_form"].validate(valid => {
        if (valid) {
          let changePwd = `${ApiSetting.change_pwd}/${this.userInfo.empId}/pwd`;
          let change_data = Object.assign({}, this.pwd_data);
          change_data.username = this.userInfo.username;
          delete change_data.verifyPwd;
          this.$http.patch(changePwd, change_data).then(res => {
            if (res && res.code == 0) {
              this.$message({
                message: ",",
                type: "success"
              });
              this.$router.push("/login");
            }
          });
        } else {
          return false;
        }
      });
    },
    // 
    login_out() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http.delete(ApiSetting.loginOut).then(res => {
          if (res) {
            if(this.timerId) clearInterval(this.timerId);
            this.$store.dispatch("setgTicket", null);
            this.$router.push("/login");
          }
        })
      })
    }
  },
  created() {
    this.timerId = setInterval(()=> {
      this.getUserInfo1()
    },10000);
    if(this.$store.state.currentUser.employeeName){
        this.employeeName=this.$store.state.currentUser.employeeName
    } 
  }
};
</script>
<style lang='less'>
@import url("./index-element.less");
</style>
<style lang="less" scoped>
@import url("./index.less");
</style>



